@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
#mainLogin .left-side img{
    width: 100%;
}

#mainLogin {

    font-family: 'Montserrat', sans-serif;

    height: 100%;
    display: flex;
    align-items: center;
}
#mainLogin .right-sde{
    width: 50%;
    position: relative;
}
#mainLogin .left-side {
    width: 50%;
    position: relative;
}

#mainLogin .input-side{
    display: flex;
    align-items: end;
    flex-direction: column;
    list-style: none;

    gap: 2em;
}
#mainLogin .input-side input{
    width: 280px;
    padding-left: 35px;
    padding-block: 5px;

    border-radius: 20px;
    border: 1px solid #ccc;
    font-size: 14px;
    outline: none;
}

#mainLogin .input-side input:hover{
    background-color: #fcfcfc;
}


#mainLogin .inputer-login-side{
    position: relative;
}


#mainLogin .buttons button{
    width: 320px;
    background-color: #F8931D;
    border: none;
    color: white;
    font-weight: bolder;
    text-transform: capitalize;
    transition: 200ms linear;
    padding: 10px;
    border-radius: 5px;

}
#mainLogin .notifications{
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;

    top: 12%;
    opacity: 0;

    z-index: 0;

    width: 320px;
    background-color: #00d12d;
    border: none;
    color: white;
    font-weight: bolder;
    text-transform: capitalize;
    transition: 200ms linear;
    border-radius: 5px;
    text-align: center;
}

#mainLogin .toggle{
    animation: shake 250ms ease-in-out;
}

@keyframes shake {
    0%{
        left: 0px;
    }

    50%{
        left: -10px;
    }

    75%{
        left: 10px;
    }

    100%{
        left: 0px;
    }
}



#mainLogin .buttons button:hover{
    transform: scale(1.05);
    background-color: #ffa743;
}

.fa-user {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    left: 10px;
    opacity: .5;
}



.fa-lock {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    left: 10px;
    opacity: .5;
}