.notFoundPage img{
    width: 45%;
    margin: 0 auto;
    animation: moveOn 3500ms infinite;
    }
    .imagerNotFound{
    transition: 120ms ease-in-out;
    display: flex;
    width: 70%;
    align-items: center;
}
.imagerNotFound:hover{

    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);

}

@keyframes moveOn {
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(0px);
    }
}

.notFoundPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
}
.notFoundPage h1{
    margin: 0;
}