@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.user-setting-profile-pannel{
    font-family: 'Montserrat', sans-serif;
}

.user-profile-container figure{

    width: 220px;
    height: 220px;
    position: relative;

    border: 4px solid #ccc;

    border-radius: 100%;

}

#changeImageBut{
    display: block;
    background-color: #ffa743;
    left: 0;
    right: 0;
    margin: 0 auto;

    bottom: 5%;
    color: #633500;
    font-weight: bolder;

    position: absolute;
    width: 15%;
    padding: 10px;
    border-radius: 20px;
    border: none;
    font-size: 15px;

    opacity: 0;
    transition: 0.2s ease-in-out;
}

#changeImageBut:hover{
    transform: scale(1.05);
    background-color: #ff6b1c;
}



#fileImage-labeled{
    background-color: #ff6b1c;
    color: white;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    position: absolute;
    border-radius: 100%;

    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);

    right: 10px;
    bottom: 10px;

}

#fileImage-labeled:hover{
    background-color: rgb(239, 239, 239);
    color: #ff6b1c;

}

#fileImage-user-profile{
    visibility: hidden;
    position: absolute;
}

.user-profile-container img{
    width: 100%;
    height: 100%;
    border-radius: 100%;
}

.user-profile-container ul{
    display: flex;
    flex-direction: column;
    gap: 1em;
    list-style: none;
}
.user-profile-container ul li{
    display: flex;
    gap: 2em;
    align-items: center;
}

.user-profile-container ul button {
    display: none;
    width: 120px;
    padding: 10px;
    border-radius: 20px;
    border: none;
    font-size: 12px;
}

.user-profile-container input {
    padding: 8px;

    width: 200px;
    border-radius: 4px;
    border: 1px solid #ccc;
}

.user-profile-container li{
    width: 70%;
    border-radius: 50px;
    box-sizing: border-box;
    padding: 10px;
    font-weight: bolder;
}