.noNews-content-page{
    font-family: 'Montserrat', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.noNews-content-page img{
    width: 50%;
    animation: shakenews 2500ms infinite;
}

.noNews-content-page button{
    background-color: #ffa743;
    margin: 10px auto;
    color: #633500;
    font-weight: bolder;
    width: 320px;
    padding: 10px;
    border-radius: 20px;
    border: none;
    font-size: 15px;
    transition: 0.2s ease-in-out;
}

.noNews-content-page button:hover{
    transform: scale(1.05);
    cursor: pointer;
}

@keyframes shakenews {
    0%{
        transform: translateY(20px);
    }
    50%{
        transform: translateY(-20px);
    }
    100%{
        transform: translateY(20px);
    }
}

.noNews-content-page p{
    margin: 0;
}