@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.mainPanel{
    width: 100%;
    height: 100%;
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    top: 0;
    left: 0;
    align-items: center;
    display: flex;
    gap: 1em;

}

.mainPanel .sideBar-home h1{
    color: #ff6b1c;
}

.mainPanel .sideBar-home button{
    display: block;
    background-color: #ffa743;
    font-weight: bolder;
    padding: 10px;
    width: 100%;
    border-radius: 20px;
    border: none;
    transition: 0.2s ease-in-out;
}

.mainPanel .sideBar-home{
    height: 100%;
    width: 320px;
    position: relative;
    box-sizing: border-box;
    padding-left: 2em;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.mainPanel .sideBar-home ul{
    padding: 0;
    list-style: none;
}
.mainPanel .sideBar-home li{
    box-sizing: border-box;
    display: flex;
    align-items: center;
    gap: 1em;
}

.mainPanel .mainPage-home{
    box-sizing: border-box;

    padding: 20px;

    overflow-y: auto;
    scroll-behavior: smooth;

    background-color: #fafafa;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    width: 75%;
    height: 95%;
    border-radius: 20px;

    margin: 0 auto;

}

.footer-main-panel{
    font-size: 10px;
    text-align: center;
}

