@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.header-login {
    font-family: 'Montserrat', sans-serif;
    z-index: 9;
    width: 100%;
    box-shadow: 0 2px 5px rgba(172, 172, 172, 0.26);
    padding: 0;
    position: absolute;
    left: 0;
    gap: 2em;
    top: 0;
}
    
.header-login nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    width: 70%;
    margin: 0 auto;
}

.links-header-login a{
    cursor: pointer;
    font-size: 12px;
    padding: 10px;
    border-radius: 5px;
    transition: 250ms ease-in-out;
    text-transform: uppercase;
}

.links-header-login a:hover{
    color: #ffffff;

    background-color: #F8931D;
    padding: 10px;

}

.header-login h1 {
    color: #F8931D;
}

.links-header-login {
    display: flex;
    gap: 3em;
    padding: 0;
}
.links-header-login a {
    color: #ccc;
    text-decoration: none;
}
